<template>
    <div class="toast" v-show="isShow">
        <div>{{message}}</div>
    </div>
</template>

<script>
//import x from ''
export default {
    name: 'Toast',
    data() {
        return {
            isShow: false,
            message: '' 
        }
    },
    methods: {
        show(message='默认文字',duration='1500'){
            this.isShow = true;
            this.message = message;
            setTimeout(() => {
                this.isShow = false;
                this.message = ''
            }, duration);
        }
    },
}
</script>

<style scoped>
    .toast{
        position: fixed;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        padding: 8px 10px;
        color: #fff;
        background-color: rgba(0, 0, 0, .75);
        z-index: 999;
    }
</style>
