export default {
    addCart(context, payload) {
        return new Promise((resolve,reject) => {
            // 查找之前的数组是否有该商品
            let oldProduct = context.state.cartList.find(item => item.goods_id == payload.goods_id)
            // 判断oldProduct
            if (oldProduct) {
                context.commit('addCounter', oldProduct)
                resolve('当前商品 + 1')
            }
            else {
                payload.count = 1
                context.commit('addToCart', payload)
                resolve("商品添加成功")
            }
            console.log(context.state.cartList).then(res => {
                console.log(res)
            })
        })
        
    },
    // 商品选中修改
    checkedChange(context,goods_id){
        let oldProduct = context.state.cartList.find(item => item.goods_id == goods_id)
        context.commit('isChecked', oldProduct)
    }
}